const load = path => require(`@/views/library/${path}.md`).default;
export default {
    sideBar: 'library',
    children: [
        {
            title: '',
            path: '/library/standard',
            component: load('standard'),
        },
        {
            title: '',
            path: '/library/ui',
            component: load('ui'),
        },
        {
            title: '',
            path: '/library/util',
            component: load('util'),
        },
        {
            title: '',
            path: '/library/publish',
            component: load('publish'),
        },
        {
            path: '/library*',
            redirect: '/library/standard',
        }
    ],
};