const load = path => require(`@/views/code-style/${path}.md`).default;
export default {
    sideBar: 'code-style',
    children: [
        {
            title: 'JavaScript',
            path: '/code-style/javascript',
            component: load('javascript'),
        },
        {
            title: 'CSS',
            path: '/code-style/css',
            component: load('css'),
        },
        {
            title: 'HTML',
            path: '/code-style/html',
            component: load('html'),
        },
        {
            title: 'Vue',
            path: '/code-style/vue',
            component: load('vue'),
        },
        {
            title: '/',
            path: '/code-style/enum',
            component: load('enum'),
        },
        {
            path: '/code-style*',
            redirect: '/code-style/javascript',
        }
    ],
};